<template>
  <div>
    <el-table
      :data="list"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      default-expand-all:false
    >
      <el-table-column prop="id" label="规格编号" width="120">
      </el-table-column>
      <el-table-column prop="specsname" label="规格名称"> </el-table-column>

      <el-table-column prop="attrs" label="规格属性">
        <template slot-scope="scope">
          <el-tag v-for="item in scope.row.attrs" :key="item" type="success">{{
            item
          }}</el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1" @click="scope.row.status=2">启用</el-button  >
          <el-button type="danger" v-else @click="scope.row.status=1">禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="id" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="size"
      :total="count"
      @current-change="changePage"
      :current-page="page"
      v-if="count>0"
    >
    </el-pagination>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert } from "../../util/alert";
import { delSpec } from "../../util/request";
export default {
  data() {
    return {
      oneAttrs: [],
    };
  },
  computed: {
    ...mapGetters({
      list: "spec/list",
      count: "spec/count",
      size: "spec/size",
      page: "spec/page",
    }),
  },
  methods: {
    ...mapActions({
      requestSpecList: "spec/specActions",
      requestCount: "spec/countActions",
      requestPage: "spec/pageActions",
      requestSize:"spec/sizeActions"
    }),
    changePage(e) {
      this.requestPage(e);
      this.requestSpecList();
    },
    edit(id) {
      this.$emit("editDetail", id);
    },
    del(id) {
      this.$confirm("确定要删除吗？", "提示", {
        confirmButtonText: "确定",
        cancel: "取消",
         type: "warning",
      })
        .then(() => {
          delSpec({ id: id }).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg);
              this.requestSpecList();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
  mounted() {
    this.requestSpecList();
    this.requestCount();
    this.requestSize(2)
  },
};
</script>

<style>
</style>


