<template>
  <div>
      <el-button type="primary" @click="add">添加</el-button>
      <v-add :info="info" ref="add"></v-add>
      <v-list @editDetail="edit($event)"></v-list>
  </div>
</template>

<script>
import vAdd from "../spec/add"
import vList from "../spec/list"

export default {
    components:{
        vAdd,
        vList
    },
    data(){
        return {
            info:{
                isShow:false,
                title:"",
                isAdd:true
            }
        }
    },
    
    methods:{
        
        add(){
            this.info.isShow=true,
            this.info.title="添加规格"
            this.info.isAdd=true
        },
        edit(id){
            this.info.isShow=true,
            this.info.title="编辑规格"
            this.info.isAdd=false
            this.$refs.add.detail(id)
        }
    }
}
</script>

<style scoped>
.el-button{
    margin: 20px 0;
}
</style>


