<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="规格名称" :label-width="formLabelWidth">
          <el-input v-model="form.specsname" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item
          v-for="(item, index) in arr"
          :key="index"
          label="规格属性"
          :label-width="formLabelWidth"
        >
          <el-input
            v-model="item.value"
            autocomplete="off"
            style="width: 70%"
          ></el-input>
          <el-button v-if="index == 0" type="primary" @click="addSpec"
            >新增规格属性</el-button
          >
          <el-button v-else type="danger" @click="delAttr(index)"
            >删除</el-button
          >
        </el-form-item>

        <el-form-item label="状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="#409eff"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" v-if="info.isAdd" @click="ok"
          >添 加</el-button
        >
        <el-button type="primary" v-else @click="update">修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { successAlert, warningAlert } from "../../util/alert";
import { addSpec, oneSpec ,updateSpec} from "../../util/request";
import { mapGetters, mapActions } from "vuex";
export default {
  props: ["info"],
  data() {
    return {
      form: {
        specsname: "",
        attrs: "",
        status: 1,
      },
      arr: [{ value: "" }],
      formLabelWidth: "120px",
      list: [],
    };
  },
  methods: {
    ...mapActions({
      requestSpec: "spec/specActions",
      requescCount: "spec/countActions",
    }),
    delAttr(i) {
      this.arr.splice(i, 1);
    },
    ok() {
      if (this.arr.some((item) => item == "")) {
        warningAlert("规格属性不能为空");
        return;
      }
      this.form.attrs = this.arr.map((item) => item.value).join(",");
      addSpec(this.form).then((res) => {
        if ((res.data.code = 200)) {
          successAlert(res.data.msg);
          this.cancel();
          this.requestSpec();
          this.requescCount();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
    update() {
        if (this.arr.some((item) => item == "")) {
        warningAlert("规格属性不能为空");
        return;
      }
      this.form.attrs = this.arr.map((item) => item.value).join(",");
        updateSpec(this.form).then(res=>{
            if(res.data.code==200){
                successAlert(res.data.msg)
                this.cancel()
                this.requestSpec();
            }else{
                warningAlert(res.data.msg)
            }
        })
    },
    cancel() {
      this.info.isShow = false;
      this.form = {
        specsname: "",
        attrs: "",
        status: 1,
      };
      this.arr = [{ value: "" }];
    },
    addSpec() {
      this.arr.push({ value: "" });
    },
    detail(id) {
      oneSpec({ id, id }).then((res) => {
        if (res.data.code == 200) {
          this.form = res.data.list[0];
          this.form.id = id;
          this.arr = [];
          var arr1 = this.form.attrs.split(",");
          for (let i in arr1) {
            this.arr.push({ value: arr1[i] });
          }
        }
      });
    },
  },
};
</script>

<style scoped>
</style>


